import { FaceSolanaTransactionPayload } from '@haechi-labs/face-types';
import { Transaction } from '@solana/web3.js';

import { Internal } from '../internal';

export class SolanaProvider {
  internal: Internal;

  constructor(internal: Internal) {
    this.internal = internal;
  }

  async getPublicKeys(): Promise<string[]> {
    return (await this.internal.sendRpc<string[]>({
      method: 'face_accounts',
    }))!;
  }

  async signAndSendTransaction(transaction: Transaction): Promise<any> {
    const txPayload: FaceSolanaTransactionPayload = {
      serializedHex: transaction.serialize().toString('hex'),
    };
    const result = await this.internal.sendRpc<any>({
      method: 'solana_sendTransaction',
      params: [txPayload],
    });

    return result!;
  }
}
