export enum Network {
  ETHEREUM = 'ethereum',
  /** @deprecated NETWORK */
  ROPSTEN = 'ropsten',
  GOERLI = 'goerli',
  POLYGON = 'polygon',
  MUMBAI = 'mumbai',
  BNB_SMART_CHAIN = 'bnb_smart_chain',
  BNB_SMART_CHAIN_TESTNET = 'bnb_smart_chain_testnet',
  KLAYTN = 'klaytn',
  BAOBAB = 'baobab',
  SOLANA = 'solana',
  SOLANA_DEVNET = 'solana_devnet',
  /** @deprecated use ETHEREUM */
  ETH_MAINNET = 'ethereum',
  /** @deprecated use ROPSTEN */
  ETH_TESTNET = 'ropsten',
  /** @deprecated use POLYGON */
  MATIC_MAINNET = 'polygon',
  /** @deprecated use MUMBAI */
  MATIC_TESTNET = 'mumbai',
  /** @deprecated use BNB_SMART_CHAIN */
  BINANCE_COIN_MAINNET = 'bnb_smart_chain',
  /** @deprecated use BNB_SMART_CHAIN_TESTNET */
  BINANCE_COIN_TESTNET = 'bnb_smart_chain_testnet',
  /** @deprecated use KLAYTN */
  KLAYTN_MAINNET = 'klaytn',
  /** @deprecated use BAOBAB */
  KLAYTN_TESTNET = 'baobab',
}

export const isSupportedNetwork = (network?: Network) => {
  switch (network) {
    case Network.ETHEREUM:
    case Network.GOERLI:
    case Network.POLYGON:
    case Network.MUMBAI:
    case Network.BNB_SMART_CHAIN:
    case Network.BNB_SMART_CHAIN_TESTNET:
    case Network.KLAYTN:
    case Network.BAOBAB:
    case Network.SOLANA:
    case Network.SOLANA_DEVNET:
      return true;
    default:
      return false;
  }
};

